import { 
  collection, 
  doc, 
  setDoc, 
  getDoc, 
  updateDoc, 
  deleteDoc, 
  query, 
  where, 
  getDocs, 
  orderBy, 
  limit, 
  Timestamp, 
  arrayUnion, 
  arrayRemove 
} from 'firebase/firestore'
import { db } from './index'

// –î–æ–¥–∞–≤–∞–Ω–Ω—è –¥–æ–∫—É–º–µ–Ω—Ç–∞ –≤ –∫–æ–ª–µ–∫—Ü—ñ—é
export const addDocument = async (collectionName, data) => {
  try {
    const docRef = doc(collection(db, collectionName))
    const documentData = {
      ...data,
      id: docRef.id,
      createdAt: Timestamp.now(),
      updatedAt: Timestamp.now()
    }
    
    await setDoc(docRef, documentData)
    return docRef.id
  } catch (error) {
    console.error('Error adding document:', error)
    throw error
  }
}

// –û—Ç—Ä–∏–º–∞–Ω–Ω—è –¥–æ–∫—É–º–µ–Ω—Ç–∞ –∑–∞ ID
export const getDocument = async (collectionName, docId) => {
  try {
    const docRef = doc(db, collectionName, docId)
    const docSnap = await getDoc(docRef)
    
    if (docSnap.exists()) {
      return { id: docSnap.id, ...docSnap.data() }
    } else {
      return null
    }
  } catch (error) {
    console.error('Error getting document:', error)
    throw error
  }
}

// –û–Ω–æ–≤–ª–µ–Ω–Ω—è –¥–æ–∫—É–º–µ–Ω—Ç–∞
export const updateDocument = async (collectionName, docId, data) => {
  try {
    const docRef = doc(db, collectionName, docId)
    await updateDoc(docRef, {
      ...data,
      updatedAt: Timestamp.now()
    })
    return true
  } catch (error) {
    console.error('Error updating document:', error)
    throw error
  }
}

// –í–∏–¥–∞–ª–µ–Ω–Ω—è –¥–æ–∫—É–º–µ–Ω—Ç–∞
export const deleteDocument = async (collectionName, docId) => {
  try {
    await deleteDoc(doc(db, collectionName, docId))
    return true
  } catch (error) {
    console.error('Error deleting document:', error)
    throw error
  }
}

// –û—Ç—Ä–∏–º–∞–Ω–Ω—è –¥–æ–∫—É–º–µ–Ω—Ç—ñ–≤ –∑ —Ñ—ñ–ª—å—Ç—Ä–∞—Ü—ñ—î—é
export const getDocuments = async (collectionName, conditions = [], orderByField = 'createdAt', orderDirection = 'desc', limitCount = 100) => {
  try {
    const constraints = []
    
    // –î–æ–¥–∞—î–º–æ —É–º–æ–≤–∏ —Ñ—ñ–ª—å—Ç—Ä–∞—Ü—ñ—ó
    conditions.forEach(condition => {
      constraints.push(where(condition.field, condition.operator, condition.value))
    })
    
    // –î–æ–¥–∞—î–º–æ —Å–æ—Ä—Ç—É–≤–∞–Ω–Ω—è —Ç–∞ –æ–±–º–µ–∂–µ–Ω–Ω—è –∫—ñ–ª—å–∫–æ—Å—Ç—ñ
    constraints.push(orderBy(orderByField, orderDirection))
    if (limitCount) {
      constraints.push(limit(limitCount))
    }
    
    const q = query(collection(db, collectionName), ...constraints)
    const querySnapshot = await getDocs(q)
    
    const documents = []
    querySnapshot.forEach((doc) => {
      documents.push({ id: doc.id, ...doc.data() })
    })
    
    return documents
  } catch (error) {
    console.error('Error getting documents:', error)
    throw error
  }
} 